@import url(https://fonts.googleapis.com/css?family=Montserrat);
html, body, #root {
  height: 100%;
  width: 100%;
  font-family: 'Monteserrat';
  font-weight: 400;
  margin: 0;
  padding: 0; }

.bare-element {
  padding: 0;
  margin: 0; }

.hide {
  opacity: 0; }

#splash-page {
  height: 100%;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: #EDF5E1; }

#splash-page svg {
  overflow: visible;
  width: 100px;
  height: 150px; }
  #splash-page svg g {
    -webkit-animation: slide 2s linear infinite;
            animation: slide 2s linear infinite; }
    #splash-page svg g:nth-child(2) {
      -webkit-animation-delay: 0.5s;
              animation-delay: 0.5s; }
      #splash-page svg g:nth-child(2) path {
        -webkit-animation-delay: 0.5s;
                animation-delay: 0.5s;
        stroke-dasharray: 0px 158px;
        stroke-dashoffset: 1px; }
  #splash-page svg path {
    stroke: url(#gradient);
    stroke-width: 20px;
    stroke-linecap: round;
    fill: none;
    stroke-dasharray: 0 157px;
    stroke-dashoffset: 0;
    -webkit-animation: escalade 2s cubic-bezier(0.8, 0, 0.2, 1) infinite;
            animation: escalade 2s cubic-bezier(0.8, 0, 0.2, 1) infinite; }

@-webkit-keyframes slide {
  0% {
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px); }
  100% {
    -webkit-transform: translateY(50px);
            transform: translateY(50px); } }

@keyframes slide {
  0% {
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px); }
  100% {
    -webkit-transform: translateY(50px);
            transform: translateY(50px); } }

@-webkit-keyframes escalade {
  0% {
    stroke-dasharray: 0 157px;
    stroke-dashoffset: 0; }
  50% {
    stroke-dasharray: 156px 157px;
    stroke-dashoffset: 0; }
  100% {
    stroke-dasharray: 156px 157px;
    stroke-dashoffset: -156px; } }

@keyframes escalade {
  0% {
    stroke-dasharray: 0 157px;
    stroke-dashoffset: 0; }
  50% {
    stroke-dasharray: 156px 157px;
    stroke-dashoffset: 0; }
  100% {
    stroke-dasharray: 156px 157px;
    stroke-dashoffset: -156px; } }

#intro {
  position: relative;
  width: 100%;
  min-height: 600px; }

#intro p {
  font-size: 7rem;
  font-family: 'PinyonScriptRegular';
  font-weight: normal;
  font-style: normal;
  color: black; }

.intro-wrapper {
  background-color: #EDF5E1;
  padding-bottom: 2%; }

#topic {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }

#intro-courses {
  width: 50%;
  margin-left: 25%; }

#intro-courses p {
  font-size: 1.2rem; }

@media screen and (max-width: 600px) and (min-width: 20px) {
  #intro {
    min-height: 500px; }
  #intro p {
    font-size: 5rem; } }

#message p {
  font-size: 2.5rem;
  color: #EDF5E1;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  text-align: center;
  padding-top: 15%;
  padding-bottom: 15%; }

.message-wrapper {
  background-color: #379683;
  padding-left: 7%;
  padding-right: 7%; }

.slideInUp {
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s; }

#usage {
  display: -webkit-flex;
  display: flex;
  font-family: 'Montserrat', sans-serif;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center; }

#usage h4, p {
  color: #05386B; }

.image-bullets {
  width: 100%;
  padding-top: 2%;
  padding-left: 15%;
  padding-right: 15%; }

.image-bullets img {
  width: 70px;
  height: 70px; }

@media screen and (max-width: 600px) and (min-width: 20px) {
  .image-bullets img {
    width: 48px;
    height: 48px; } }

.section-center {
  padding-top: 5%; }

.image-bullet-topics {
  border-bottom: 2px solid #379683; }

@-webkit-keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.fade-in {
  opacity: 0;
  /* make things invisible upon start */
  -webkit-animation: fadeIn ease-in 1;
  /* call our keyframe named fadeIn, use animattion ease-in and repeat it only 1 time */
  animation: fadeIn ease-in 1;
  -webkit-animation-fill-mode: forwards;
  /* this makes sure that after animation is done we remain at the last keyframe value (opacity: 1)*/
  animation-fill-mode: forwards;
  -webkit-animation-duration: 0.5s;
  animation-duration: 0.5s; }

.page {
  width: 100%;
  height: 100%; }

